.SignUps__area {
  padding-top: 200px;
  margin-bottom: 100px;
}

.SignUps {
  border: 1px solid #dbdbdb;
  width: 350px;
  height: 540px;
  background-color: #ffffff;
  border-radius: 15px;
  text-align: center;
}

.SignUp {
  margin-bottom: 13px;
}

.SignUps__body {
  background-color: #ffffff;
}

.SignUps__title {
  font-weight: 600;
  font-size: 30px;

  margin: 30px 0px;
}

.SignUps__input {
  width: 270px;
  height: 40px;
  background-color: #fafafa;
  border: 1px solid #dbdbdb;
  border-radius: 4px;
}

.SignUps__passwordB {
  padding-bottom: 11px;
}

.SignUps__btn {
  width: 280px;
  height: 30px;
  background-color: #fbd46d;
  border: 1px solid #dbdbdb;
  border-radius: 5px;
}

.SignUp__error {
  padding-top: 19px;
  color: #c83c0d;
  font-weight: 500;
  font-family: "Jua", sans-serif;
}

.is-valid {
  box-shadow: 0px 0px 10px rgb(10, 233, 40);
}

.is-invalid {
  box-shadow: 0px 0px 10px red;
}

.SignUps__passwordValid {
  color: #0bdd27;
  margin-top: 10px;
  padding-right: 140px;
  font-family: "Jua", sans-serif;
}

.SignUps__passwordInvalid {
  color: #c83c0d;
  margin-top: 10px;
  padding-right: 96px;
  font-family: "Jua", sans-serif;
}

.navs {
