}

.Details {
  display: block;
}

.Details__body {
  display: flex;
  justify-content: center;
  padding-top: 50px;
  top: 0;
  height: 550px;
}

.Details__picArea {
  width: 400px;
  margin-right: 50px;
}
.Details__pic {
  width: 90%;
  border-radius: 10px;
}
