}

li {
  list-style: none;
}

.Details__commentsArea {
  box-sizing: border-box;
  border: 2px solid black;
  border-radius: 10px;
  width: 1240px;
  text-align: left;
  padding: 30px 50px;
  margin-bottom: 300px;
}

.Details__comment {
  font-size: 18px;
  margin-bottom: 20px;
  color: rgb(59, 38, 175);
}

.Details__comment::after {
  content: "";
  display: block;
  width: 100%;
  border-bottom: 1px solid #bcbcbc;
  margin: 20px auto;
}

.Details__commentsHead {
  width: 67%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.Details__commentPic {
  height: 150px;
}

.Details__commentTextEdit {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

